export default class PluginPIP {
  static pluginName = 'pluginPIP'

  constructor(player) {
    if (!document.pictureInPictureEnabled) {
      console.warn('PIP not enabled')
      return
    }
    this.player = player
    this.video = player.video
    let bcr = this.video.getBoundingClientRect()
    let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
    this.video2Top = bcr.bottom + scrollTop
    this.scrollHandler = this._handleScrollPip.bind(this)
    this.init()
  }

  init() {
    window.addEventListener('scroll', this.scrollHandler)
    this.video.addEventListener('enterpictureinpicture', () => {
      this.isPip = true
    })
    this.video.addEventListener('leavepictureinpicture', () => {
      this.isPip = false
    })
    this.player.togglePIP = this._togglePip.bind(this)
  }

  _handleScrollPip() {
    let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
    if (
      (scrollTop >= this.video2Top && !this.isPip) ||
      (scrollTop < this.video2Top && this.isPip && this.autoHide)
    ) {
      this._togglePip()
    }
  }

  async _togglePip() {
    try {
      if (this.video !== document.pictureInPictureElement) {
        await this.video.requestPictureInPicture()
      } else {
        await document.exitPictureInPicture()
      }
    } catch (e) {
      console.warn(e.message)
    }
  }

  destroy() {
    window.removeEventListener('scroll', this.scrollHandler)
  }
}
