import {reactive} from 'vue'
import {getSearchParams} from '../shared/utils'
import PluginPIP from '../plugins/PluginPIP.js'
import PluginEvents from '../plugins/PluginEvents.js'

export function usePlayerState() {
  let qs = getSearchParams()
  console.log('ana querystring', qs)

  const playerParams = reactive({
    appId: qs.appId,
    // live | vod options
    type: qs.type || 'live',
    roomId: qs.roomId,
    playerType: qs.playerType || 'auto',
    recordId: qs.recordId,
    // video options
    autoplay: true,
    controls: 'custom',
    muted: false,
    poster: '',
  })

  function createPlayer() {
    if (window.vhPlayer) {
      window.vhPlayer.destroy()
    }
    let options = {
      videoNode: 'vh-player',
      type: playerParams.type,
      autoplay: playerParams.autoplay,
      controls: playerParams.controls,
      muted: playerParams.muted,
      poster: playerParams.poster,
      debug: true,
      appId: playerParams.appId, // 'N5oMU37i' : 'UNUBw1EB'
      accountId: 405,
      token: 'vhall',
      liveOption: {
        roomId: playerParams.roomId, // 'lss_jZqjePJY' : 'lss_XeTu8XDD'
        type: playerParams.playerType
      },
      vodOption: {
        recordId: playerParams.recordId // 58faa1f6
      },
      scheduleOption: {
        enable: true,
        debugCDN: playerParams.cdn || 'tx',
        debugPlayer: playerParams.playerType // hlsnative | hls | dash
      },
      // marqueeOption: {
      //   enable: true,
      //   type: 1,
      //   text: 'test marquee'
      // },
      // vttOptions: {
      //   enable: true,
      //   tracks: [
      //     {label: '英文', kind: 'subtitles', srclang: 'en', src: location.pathname + 'vtt/subtitles_en.vtt'},
      //     {label: '中文', kind: 'subtitles', srclang: 'zh', src: location.pathname + 'vtt/subtitles_zh.vtt', default: true},
      //   ]
      // },
      // uiOptions: {
      //   line: {
      //     tx: '腾讯线路',
      //     ali: '备用线路1',
      //     hw: '备用线路2',
      //     cy: '备用线路3',
      //   },
      //   quality: {
      //     same: '原画',
      //     '720p': '高清',
      //     '480p': '标清',
      //     '360p': '流畅',
      //     a: '音频',
      //   }
      // }
    }
    console.log('options:', options)
    window.VhPlayer.use(PluginEvents)
    window.VhPlayer.use(PluginPIP)
    window.VhPlayer.createInstance(options, _player => {
      console.log('createInstance success', _player)
      window.vhPlayer = _player
    }, err => {
      console.warn('createInstance fail', err)
    })
  }

  async function togglePIP() {
    return window.vhPlayer.togglePIP()
  }

  return {
    playerParams,
    createPlayer,
    togglePIP
  }
}
