export default class PluginEvents {
  static pluginName = 'pluginEvents'

  constructor(player) {
    this.player = player
    this.register()
  }

  register() {
    this.player.on(window.VhPlayer.Events.SCHEDULER_COMPLETE, (data) => {
      console.log('sdk调度完成 =>', data)
    })

    this.player.on(window.VhPlayer.Events.Ended, (data) => {
      console.log('播放结束 =>', data)
      this.player.exitFullscreen()
    })
  }

  init() {}
  destroy() {}
}