<template>
  <div id="vh-player"></div>
  <div id="form" class="mb4">
    <w-input class="mb2" label="appId" shadow outline v-model="playerParams.appId"></w-input>
    <w-input class="mb2" label="roomId" shadow outline v-model="playerParams.roomId"></w-input>
    <w-input class="mb2" label="recordId" shadow outline v-model="playerParams.recordId"></w-input>
    <div class="mb2">
      <w-radios :items="radiosType" inline v-model="playerParams.type"></w-radios>
    </div>
    <div class="mb2">
      <w-radios :items="radiosPlayerType" inline v-model="playerParams.playerType"></w-radios>
    </div>
    <div class="mb2">
      <w-radios :items="radiosControls" inline v-model="playerParams.controls"></w-radios> <span class="ml2">(控制栏)</span>
    </div>
    <div class="mb2">
      <w-checkbox v-model="playerParams.autoplay">自动播放</w-checkbox>
    </div>
    <div class="mb2">
      <w-checkbox v-model="playerParams.muted">静音</w-checkbox>
    </div>

    <w-button lg bg-color="info" @click="createPlayer">init</w-button>
  </div>

  <p>自定义操作</p>
  <div id="custom-controls">
    <w-button lg bg-color="info" @click="togglePIP">PIP</w-button>
  </div>
</template>
<script>
import {defineComponent} from 'vue'
import {usePlayerState} from './PlayerState'

export default defineComponent({
  name: 'Player',
  setup(props) {
    let {playerParams, createPlayer, togglePIP} = usePlayerState(props)
    return {
      playerParams,
      createPlayer,
      togglePIP,
      radiosType: [
        {label: 'live'},
        {label: 'vod'},
      ],
      radiosPlayerType: [
        {label: 'auto'},
        {label: 'flv'},
        {label: 'hls'},
      ],
      radiosControls: [
        {label: '自定义', value: 'custom'},
        {label: '原生', value: 'native'},
        {label: '不显示', value: 'none'},
      ]
    }
  }
})
</script>
